/**
 * Rotates `x` `n` bits to the left.
 *
 * @param x - value
 * @param n - rotation step
 */
export const rotateLeft = (x, n) => ((x << n) | (x >>> (32 - n))) >>> 0;
/**
 * Rotates `x` `n` bits to the right.
 *
 * @param x - value
 * @param n - rotation step
 */
export const rotateRight = (x, n) => ((x >>> n) | (x << (32 - n))) >>> 0;
